
// Import bootstrap
@import "~nr1-bootstrap-theme/dist/main.css";

.jumbotron {
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin-bottom: 0;
    background-color: #fff;
}

@media (min-width: 768px) {
    .jumbotron {
        padding-top: 6rem;
        padding-bottom: 6rem;
    }
}

.jumbotron p:last-child {
    margin-bottom: 0;
}

.jumbotron h1 {
    font-weight: 300;
}

.jumbotron .container {
    max-width: 40rem;
}

footer {
    padding-top: 3rem;
    padding-bottom: 3rem;
}

footer p {
    margin-bottom: .25rem;
}

.bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
}

.sources .badge {
    margin-right: 5px;
}

.float-right {
    float: right;
}

.code {
    width: 100%;
}

.sidebar {
    border-right: 1px solid #eee;
}

.nav-divider {
    border-bottom: 1px solid #eee;
    padding-top: 5px;
    margin-bottom: 5px;
}

.header {
    border-bottom: 1px solid #eee;
    padding: 10px 0 15px 0;
}

header a:hover {
    text-decoration: none;
}

h1 {
    font-size: 2em;
}

h4 {
    padding: 10px 0 5px 0;
}

h5 {
    padding: 15px 0 5px 0;
}

.text-small {
    font-size: 0.8em;
}

.text-red {
    color: red;
}

.description {
    padding: 10px;
}

.loading {
    padding-top: 50px;
}

.card-title {
    padding: 3px 0 5px 0;
}
